.view-candidates {
    padding: 1em;
}

.selection-screen {
    display: flex;
    flex-direction: column;
    gap: 2em;
}

.selectors {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
}

.city-council-,
.school-committee-,
.mayor- {
    font-size: 10px;
    padding: 8px 3px;
    min-width: 125px;
    text-decoration: none;
    border: none;
    border-radius: 3px;
    background-color: var(--cta-purple);
    color: white;
}

.city-council-selected,
.school-committee-selected,
.mayor-selected {
    font-size: 10px;
    padding: 8px 3px;
    min-width: 125px;
    text-decoration: none;
    border: 1px solid white;
    border-radius: 3px;
    background-color: var(--cta-purple);
    color: white;
}

.candidates {
    display: flex;
    flex-direction: row;
    gap: 0.75em;
    flex-wrap: wrap;
    /* grid-template-columns: repeat(auto-fit, minmax(125px, 1fr)); */
}

