.hero {
    display: flex;
    flex-direction: column;
    padding: 1em;
    gap: 10px;
}

.logo {
    height: 3em;
    width: 10em;
}

.landing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    height: 100vh;
    width: 100%;
}

.header {
    color: white;
    font-weight: 700;
    font-size: 50px;
    text-align: center;
}

.info-section {
    display: flex;
    flex-direction: column;
    gap: 2px;
    width: 50%;
}

.title {
    color: var(--white);
    font-size: 20px;
}

.subtitle {
    font-size: 14px;
    color: var(--gray);
}

.info {
    font-size: 12px;
    color: lightgray;
}

.landing--cta {
    background-color: var(--cta-purple);
    color: var(--white);
    min-width: 90px;
    text-align: center;
    padding: 3px;
    font-size: 12px;
    border-radius: 3px;
    border: none;
    cursor: pointer;
    text-decoration: none;
}

.cta-section {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 1em;
}

.info-img {
    width: 30px;
    height: 30px;
}

.down-arrow {
    position: absolute;
    top: calc(100% - 60px);
    left: calc(50%);
    width: 0;
    height: 30px;
    border: 1px solid;
    border-radius: 1px;
    color: white;
    animation: jumpInfinite 2s infinite;
}

.down-arrow:after {
    content: " ";
    position: absolute;
    top: 12px;
    left: -8px;
    width: 16px;
    height: 16px;
    border-bottom: 2px solid;
    border-right: 2px solid;
    border-radius: 2px;
    transform: rotateZ(45deg);
}

@keyframes jumpInfinite {
    0% {
        margin-top: 0;
    }
    50% {
        margin-top: 10px;
    }
    100% {
        margin-top: 0;
    }
}

#election-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
}

#election-info > h1 {
    text-align: center;
    font-size: 30px;
    font-weight: 500;
}

.information {
    display: flex;
    flex-direction: column;
    gap: 1em;
    width: 75%;
}

.info-header {
    font-size: 21px;
    color: white;
    font-weight: 500;
}

.info-snippet {
    display: flex;
    flex-direction: row;
    gap: 0.75em;
    color: white;
}

.date {
    font-size: 15px;
    font-weight: 200;
}

.link {
    color: #d9d9d9;
    font-size: 15px;
    font-weight: 200;
}

