:root {
    --dark-purple: #221b27;
    --light-dark-purple: #3d3452;
    --cta-purple: #7632a9;
    --secondary-purple: #32243c;
    --white: #ffffff;
    --pink: #ff7f8b;
    --gray: #d9d9d9;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
    font: inherit;
}

html {
    scroll-behavior: smooth;
}

h1,
h2,
h3,
p,
blockquote,
a,
input,
textarea,
button,
select,
span {
    font-family: "IBM Plex Sans", sans-serif;
    margin: 0;
}

/* body {
    margin: 0;
    height: 100%;
    background-color: var(--dark-purple);
} */

body {
    background: linear-gradient(
        -45deg,
        var(--dark-purple),
        var(--light-dark-purple),
        var(--dark-purple)
    );
    background-size: 400% 400%;
    animation: gradient 8s ease infinite;
    height: 100vh;
    max-width: 1920px;
    margin: 0 auto;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
